@import './variables.module.scss';

.navbar {
    margin-bottom: 0;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 20;
}

.link {
    text-decoration: none;
}

.list {
    display: flex;
    margin: 0;
    background-color: white;
    padding: 2rem;
    justify-content: flex-end;
}

.listItem {
    color: black;
    list-style-type: none;
    margin: 0 2rem;
    font-size: 18px;
}

.languageBtn {
    all: unset;
    border: none;
    background: transparent;
    margin: 0 10px;
    cursor: pointer;

    & :hover {
        color: $accent-color;
    }
}

