@import './variables.module.scss';

.projectImg {
    width: 350px;
}

.projectContent {
    display: flex;
    flex-direction: column;
    gap: 0px;
    margin-bottom: 30px;
    padding-bottom: 30px;
    border-bottom: 1px solid $light-violet;

    &:last-child {
        margin-bottom: none;
    }

    @media screen and (min-width: 768px) {
        flex-direction: row;
        gap: 60px;
    }
}


.projectInfoContainer {
    display: flex;
    flex-direction: column;
    max-width: 600px;
}

.projectTitle {
    font-size: 30px;
    margin-bottom: 0;

    @media screen and (max-width: 768px) {
        font-size: 24px;        
    }
}

.projectTech {
    margin-top: 0;
    font-weight: 300;
    color: $dark-grey;
}

.tasksList {
    list-style: none;
    padding: 0;
    margin-top: 0;
}

.tasksTitle {
    margin-bottom: 5px;
    font-weight: 500;
}


.title {
    font-size: 40px;
    font-weight: 800;
    position: relative;
    display: initial;

    &:before {
        width: 100%;
        height: 7px;
        content: "";
        background: $medium-violet;
        position: absolute;
        bottom: 0px;
        left: -3px;
        z-index: -1;  
    }
}

