@import './variables.module.scss';

.skill {
    padding: 8px 20px;
    background-color: $dark-violet;
    color: $primary-color;
    margin: 0 10px 25px 0;
    border-radius: 50px;
    display: inline-block;
    letter-spacing: 0.5px;
}

.subtitle {
    font-weight: 400;
    letter-spacing: 0.5px;
    margin: 0 0 10px;
}

.certification {
    margin-bottom: 15px;

    &:last-child {
        margin-bottom: 0;
    }
}

.period {
    margin-right: 5px;
    padding-right: 5px;
    border-right: 1px solid $dark-violet;
    font-weight: 200;
}

.institution {
    display: block;
    font-weight: 500;
}

.link {
    color: #000;
    text-decoration: none;
    font-weight: 600;

    &:hover {
        text-decoration: underline;
    }
}
