@import './variables.module.scss';
@import url('https://fonts.googleapis.com/css2?family=EB+Garamond:wght@400;500;600;700;800&family=Jost:wght@100;200;300;400;500;600;700;800;900&family=Manrope:wght@200;300;400;500;600;700;800&family=Nunito:wght@200;300;400;500;600;700;800&family=Raleway:wght@200;300;400;500;600;700;800;900&display=swap');

body {
    box-sizing: border-box;
    font-family: 'Manrope', sans-serif;
    margin: 0;
}

html {
    scroll-behavior: smooth;
}

footer {
    height: 150px;
    background-color: $green;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 88px;

    p {
        color: $sand;
        letter-spacing: 0.5px;
    }

    @media screen and (max-width: 768px) {
        margin-top: 40px;
    }
}

.footer-year {
    font-weight: 200;
    margin-right: 10px;
    padding-right: 10px;
    border-right: 1px solid $dark-violet;
}
.title {
    font-size: 40px;
    font-weight: 800;
    position: relative;
    display: inline-block;
    margin: 0 0 25px;

    &:before {
        width: 100%;
        height: 7px;
        content: "";
        background: $medium-violet;
        position: absolute;
        bottom: 0px;
        left: -3px;
        z-index: -1;  
    }

    @media screen and (max-width: 768px) {
        font-size: 30px;
    }
}

.container {
    width: 90%;
    max-width: 1000px;
    margin: 0 auto;
    padding-top: 88px;

    @media screen and (max-width: 768px) {
        padding-top: 40px;
    }
}

.ctaIcon {
    margin-left: 10px;
}

.cta {
    color: $accent-color;
    font-weight: 500;
    border: 1px solid $accent-color;
    padding: 10px 25px;
    width: max-content;
    border-radius: 8px;
    text-decoration: none;
    display: inline-block;
}

//menu
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    right: 36px;
    top: 36px;
}

.bm-burger-bars {
    background: $medium-violet;
    border-radius: 20px;
}

.bm-menu {
    background-color: $green;
}

.bm-item-list {
    margin-top: 40px;
    height: calc(100% - 40px);
}

.bm-item {
    padding: 20px 0;
    color: $sand;
    border-bottom: 1px solid $sand;
    margin: 0 20px;
}

.bm-menu-wrap {
    width: 200px;
}

.bm-cross-button {
    height: 24px;
    width: 24px;
  }

  .bm-cross {
    background: $sand;
    width: 2px !important;
    height: 25px !important;
  }
