@import './variables.module.scss';

.container {
    background: url(../../public/Mohnwiese2.jpg);
    height: 100vh;
    background-position: center;
    background-size: cover;
}

.cardContainer {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cardContent {
    background-color: #fdfdfd;
    padding: 60px;
    border-radius: 8px;
    max-width: 650px;
    margin-top: 90px;
    width: 70%;

    @media screen and (max-width: 640px) {
        padding: 40px;
    }

    @media screen and (max-width: 425px) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.title {
    font-weight: 200;
    font-size: 40px;
    margin-top: 0;

    @media screen and (max-width: 640px) {
        font-size: 30px;
    }
}

.titleBold {
    font-weight: 900;
}

.socialMediaIcon {
    width: 35px;
    height: 35px;
    color: black;
    margin-right: 20px;
    transition: all 500ms;

    &:hover {
        color: $accent-color;
    }
}

.introduction {
    @media screen and (max-width: 767px) {
        display: none;
    }
}